import { React, PropTypes, GoogleLogout } from "../includes/Commons";
import { styles, withStyles, Paper, Button, Typography } from "../theme/styles/index";
import { googleLogout } from "../includes/Functions";
import { clientId } from "../includes/Constants";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class Logout extends React.Component {
  
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 state = { redirect: null };
 static contextTypes = { router: PropTypes.object };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {
  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  const { classes } = this.props;

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <Paper className={classes.paper}>
    <div className={classes.contentWrapper}>
     <h3 key="Subheader" cols={2} style={{ height: "auto" }}>
      Getting out of the System
     </h3>
     <Typography>
      Google Sign-In manages the OAuth 2.0 flow and token lifecycle, simplifying your integration with Google APIs. A user always has the option to revoke
      access to an application at any time. <br /> <br />
     </Typography>
     <Typography>
      <GoogleLogout
       clientId={clientId}
       onLogoutSuccess={googleLogout(this, "/login")}
       render={renderProps => (
        <Button onClick={renderProps.onClick} variant="contained" size="small" color="primary">
         Are you sure to log-out
        </Button>
       )}
       buttonText="Logout"
      />
     </Typography>
    </div>
   </Paper>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

Logout.propTypes = {
 classes: PropTypes.object.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(Logout);
