import { React, PropTypes, Link, Fragment } from "../includes/Commons";
import { styles, withStyles, Paper, Button, Typography } from "../theme/styles/index";
import { userIsLoggedIn, getUser } from "../includes/Functions";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class Login extends React.Component {
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 state = {
  signUp: localStorage.getItem('signUp'),
  isUser: userIsLoggedIn(),
  user: getUser()
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 static contextTypes = { router: PropTypes.object };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {
  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  const This = this;
  const { classes } = This.props;
  const { signUp } = This.state;

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <Paper className={classes.paper}>
    <div className={classes.contentWrapper}>
    <Fragment>
     <h3 key="Subheader" cols={2} style={{ height: "auto" }}>
      Already registered, sign-in
     </h3>   
     <Typography>
      Google Sign-In manages the OAuth 2.0 flow and token lifecycle, simplifying your integration with Google APIs. A user always has the option to revoke
      access to an application at any time. <br /> <br />
      <Button component={Link} to={`/access`} variant="contained" size="small" color="primary">
         Login
        </Button>
     </Typography>
     </Fragment>
     <br />
     { !signUp ? 
      <Fragment> 
     <h3 key="Subheader" cols={2} style={{ height: "auto" }}>
      New to the system, Sign-Up
     </h3>   
     <Typography>
      <Link to={`/register`} variant="contained" size="small" color="primary" style={{color:"#2196f3"}}>
       Register
      </Link>
     </Typography>
     </Fragment>
      : ''}
    
    </div>
   </Paper>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

Login.propTypes = {
 classes: PropTypes.object.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(Login);
