import { React, PropTypes, Fragment } from "../includes/Commons";
import { styles, withStyles, Modal, Button, Typography } from "../theme/styles/index";
import { handleCloseModal, getModalStyle } from "../includes/Functions";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class ModalReducer extends React.Component {
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 state = {
  open: false,
  message: null,
  anchorEl: null
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 static contextTypes = {
  router: PropTypes.object
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {
  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  const This = this.props.this;
  const { classes } = This.props;
  const { message, open } = This.state;
  const CloseModal = e => handleCloseModal(This);
  const ModalStyle = getModalStyle();

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <Fragment>
    <Modal aria-labelledby="simple-modal-title" aria-describedby="simple-modal-description" open={open} onClose={CloseModal}>
      <div style={ModalStyle} className={classes.paperModal}>
       <h3>Message</h3>
       <Typography variant="subtitle1" id="simple-modal-description" style={{fontSize: 14}}>
        {message}. This modal is diplayed whenever any action has been taken by the user.
       </Typography>
       <br />
       <Typography variant="subtitle1" style={{fontSize: 14}}>
        <Button onClick={CloseModal} variant="contained" size="small" color="primary"> Well noted. </Button>
        </Typography>
      </div>
     </Modal>
   </Fragment>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

ModalReducer.propTypes = {
 classes: PropTypes.object.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(ModalReducer);
