import { React, PropTypes, Link } from "../includes/Commons";
import { styles, withStyles, ListItemIcon, ListItem, Divider, Drawer, HomeIcon, List, ListItemText, classNames } from "../theme/styles/index";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

import { LeftMenus } from "../includes/Menus";
import { userIsLoggedIn } from "../includes/Functions";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class LeftSidebar extends React.Component {
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 state = {
  isUser: userIsLoggedIn(),
  mobileOpen: false
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {
  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  const This = this;
  const { isUser } = This.state;
  const { onDrawerToggle, classes, ...other } = This.props;

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <Drawer variant="permanent" {...other}>
    <List disablePadding>
     <ListItem className={classNames(classes.firebase, classes.item, classes.itemCategory)}>WBUDDY</ListItem>
     <ListItem className={classNames(classes.item, classes.itemCategory)}>
      <ListItemIcon>
       <HomeIcon />
      </ListItemIcon>
      <ListItemText
       classes={{
        primary: classes.itemPrimary
       }}
      >
       Welcome
      </ListItemText>
     </ListItem>
     <br />
     <React.Fragment key="MainMenu">
      {LeftMenus.map(({ id: childId, icon, active, path, access }) =>
       isUser || (!access && !isUser) ? (
        <ListItem
         to={path}
         component={Link}
         onClick={onDrawerToggle}
         button
         dense
         key={childId}
         className={classNames(classes.item, classes.itemActionable, active && classes.itemActiveItem)}
        >
         <ListItemIcon>{icon}</ListItemIcon>
         <ListItemText
          classes={{
           primary: classes.itemPrimary,
           textDense: classes.textDense
          }}
         >
          {childId}
         </ListItemText>
        </ListItem>
       ) : (
        ""
       )
      )}
      <Divider className={classes.divider} />
     </React.Fragment>
    </List>
   </Drawer>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

LeftSidebar.propTypes = {
 classes: PropTypes.object.isRequired,
 onDrawerToggle: PropTypes.func.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(LeftSidebar);