import { React, PropTypes, Link, Avatars } from "../includes/Commons";
import { styles, withStyles, AppBar, Hidden, Grid, IconButton, NotificationsIcon, MenuIcon, Tab, Tabs, Toolbar, Tooltip, Typography } from "../theme/styles/index";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

import { TopMenus } from "../includes/Menus";
import { userIsLoggedIn, getUser } from "../includes/Functions";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class TopHeader extends React.Component {
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 state = {
  title: null,
  value: "Home",
  isUser: userIsLoggedIn(),
  user: getUser()
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 static contextTypes = {
  router: PropTypes.object
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 handleChange = (event, value) => { this.setState({ value }); this.setState(state => ({ title: value })); };
 handleChangeIndex = index => { this.setState({ value: index }); };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  const { title, value, isUser } = this.state;
  const { name, email } = this.state.user;
  const { classes, onDrawerToggle } = this.props;

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <React.Fragment>
    <AppBar color="primary" position="sticky" elevation={0}>
     <Toolbar>
      <Grid container spacing={8} alignItems="center">
       <Hidden smUp>
        <Grid item>
         <IconButton color="inherit" aria-label="Open drawer" onClick={onDrawerToggle} className={classes.menuButton}>
          <MenuIcon />
         </IconButton>
        </Grid>
       </Hidden>
       <Grid item xs />
       <Grid item>
        <Typography className={classes.link} component={Link} to="/profile">
         {name}
        </Typography>
       </Grid>
       <Grid item>
        <Tooltip title="Alerts  No alters">
         <IconButton color="inherit">
          <NotificationsIcon />
         </IconButton>
        </Tooltip>
       </Grid>
       <Grid item>
        <IconButton color="inherit" className={classes.iconButtonAvatar} component={Link} to="/profile">
         {email ? <Avatars email={email} className={classes.avatar} size={32} round="20px" /> : ""}
        </IconButton>
       </Grid>
      </Grid>
     </Toolbar>
    </AppBar>
    <AppBar component="div" className={classes.secondaryBar} color="primary" position="static" elevation={0}>
     <Toolbar>
      <Grid container alignItems="center" spacing={8}>
       <Grid item xs>
        <Typography color="inherit" variant="h5">
         Home | {title}
        </Typography>
       </Grid>
      </Grid>
     </Toolbar>
    </AppBar>
    <AppBar component="div" className={classes.secondaryBar} color="primary" position="static" elevation={0}>
     <Tabs value={value} textColor="inherit" onChange={this.handleChange}>
      {TopMenus.map(({ id: childId, path, access = false }) =>
       isUser || (!access && !isUser) ? <Tab textColor="inherit" label={childId} value={childId} component={Link} to={path} key={childId} /> : ""
      )}
     </Tabs>
    </AppBar>
   </React.Fragment>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

TopHeader.propTypes = {
 classes: PropTypes.object.isRequired,
 onDrawerToggle: PropTypes.func.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(TopHeader);
