import { SearchReducer } from "../reducers";
import { React, PropTypes, Link, Fragment, Avatars } from "../includes/Commons";
import { classNames, styles, withStyles, Fab, AddIcon, EditIcon, MoreVertIcon, Paper, AppBar, Toolbar, DeleteIcon, Grid, CardContent, Typography, IconButton, Card, CardHeader, CardActions, FavoriteIcon } from "../theme/styles/index";
import { userIsLoggedIn, likeContact, deleteContact } from "../includes/Functions";


/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

import { fetchContacts, handleSearchInput } from "../includes/Functions";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class Contacts extends React.Component {
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 state = {
  search: '',
  buttons: {},
  contacts: [],
  anchorEl: null,
  isUser: userIsLoggedIn()
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 componentDidMount() { fetchContacts(this); }
 handleClose = () => { this.setState({ anchorEl: null }); };
 handleClick = event => { this.setState({ anchorEl: event.currentTarget });};
 handleTopSearchInput(event, e){ var check = handleSearchInput(event, e); if(check){ fetchContacts(this); } }

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {
  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  const This = this;
  const { classes } = This.props;
  const { contacts, anchorEl, buttons, isUser } = This.state;
  const open = Boolean(anchorEl);

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <Fragment>
    <Paper className={classes.paper}>
     <AppBar className={classes.searchBar} position="static" color="default" elevation={0}>
      <Toolbar>
       <Grid container spacing={16} alignItems="center">
        <SearchReducer this={This} Change={(event, e) => this.handleTopSearchInput(event, This)} />
       </Grid>
      </Toolbar>
     </AppBar>
     <div className={classes.contentWrapper}>
      <h3>All Contacts</h3>
      {contacts.map(({ email, firstname, surname, vid, timestamp, bio, likes, fans }) => (
       <Card className={classes.card} key={vid}>
        <CardHeader
         avatar={firstname[0] ? 
          <Avatars color="gray" name={firstname[0]} aria-label="Recipe" className={classes.avatar} size={32} round="20px">
           {firstname[0]}
          </Avatars> : ''
         }
         action={
          <Fragment>
           <IconButton
            component={Link}
            to={`/view/${vid}`}
            className={classNames(classes.expand, {
             [classes.expandOpen]: this.state.expanded
            })}
            aria-label="More"
            aria-owns={open ? { surname } : undefined}
            aria-haspopup="true"
            onClick={this.handleClick}
           >
            <MoreVertIcon />
           </IconButton>
          </Fragment>
         }
         title={`${firstname} ${surname}`}
         subheader={Intl.DateTimeFormat("en-GB", {
          year: "numeric",
          month: "long",
          day: "2-digit"
         }).format(timestamp)}
        />
        <Link to={`/view/${vid}`}>
         <Avatars className={classes.avatarSpecial} email={email} name={`${firstname} ${surname}`} width="100%" size="330" color="gray" />
        </Link>

        <CardContent>
         <Typography component="p" className={classes.fontTiny}>
          Email | {email} | {likes} like(s)
         </Typography>
         <Typography component="p">{bio}</Typography>
        </CardContent>
        <CardActions className={classes.actions} disableActionSpacing>
         <IconButton disabled={buttons[vid] || fans.indexOf(isUser) !== -1} aria-label="Add to favorites" onClick={(event, e) => likeContact(This, vid)}>
          <FavoriteIcon />
         </IconButton>
         <IconButton aria-label="Share" onClick={(e, vid, path) => deleteContact(This, vid, '/contacts')}>
          <DeleteIcon />
         </IconButton>
         <IconButton aria-label="Share"  component={Link} to={`/edit/${vid}`}>
          <EditIcon />
         </IconButton>
        </CardActions>
       </Card>
      ))}
     </div>
    </Paper>
    <Fab className={classes.fab} color="primary" component={Link} to="/add">
     <AddIcon />
    </Fab>
   </Fragment>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

Contacts.propTypes = {
 classes: PropTypes.object.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(Contacts);
