import { ModalReducer } from "../reducers";
import { React, Fragment, PropTypes } from "../includes/Commons";
import { styles, withStyles, AppBar, Toolbar, Paper, Grid, TextField, FormControlLabel, FormLabel, Button, RadioGroup, Radio } from "../theme/styles/index";
import { insertContact } from "../includes/Functions";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class Add extends React.Component {
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 constructor(props) {
  super(props);
  this.onSubmit = this.onSubmit.bind(this);
  this.onChangeFieldValue = this.onChangeFieldValue.bind(this);
  this.state = {
   message: null,
   open: false,
   redirect: false,
   contact: { firstname: "", middlename: "", surname: "", email: "", bio: "", timestamp: "", gender: "Female" }
  };
 }

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 static contextTypes = { router: PropTypes.object };
 onSubmit(e) { e.preventDefault(); insertContact(this, "/contacts"); }
 onChangeFieldValue(event, value) { this.setState({ contact: { ...this.state.contact, [event.target.id]: event.target.value } }); }
 handleChangeSelect = (event, value) => { this.setState({ contact: { ...this.state.contact, [event.target.name]: value } }); };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {
  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  const This = this;
  const { classes } = This.props;
  const { firstname, middlename, surname, email, bio, gender, timestamp } = This.state.contact;

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <Fragment>
    <Paper className={classes.paper}>
     <AppBar className={classes.searchBar} position="static" color="default" elevation={0}>
      <Toolbar>
       <Grid container spacing={16} alignItems="center" />
      </Toolbar>
     </AppBar>
     <div className={classes.contentWrapper}>
      <h3>Adding New Contact</h3>
      <form className={classes.container} noValidate autoComplete="off" onSubmit={this.onSubmit}>
      
      <TextField
        id="timestamp"
        label="TIMESTAMP"
        onChange={this.onChangeFieldValue}
        className={classes.hiddenTextField}
        fullWidth
        value={timestamp}
        margin="normal"
        InputProps={{
         classes: {
          input: classes.tinyFont
         }
        }}
       />  

       <TextField
        id="firstname"
        label="First Name"
        onChange={this.onChangeFieldValue}
        className={classes.textField}
        fullWidth
        value={firstname}
        margin="normal"
        InputProps={{
         classes: {
          input: classes.tinyFont
         }
        }}
       />
       <TextField
        id="middlename"
        label="Middle Name"
        className={classes.textField}
        onChange={this.onChangeFieldValue}
        fullWidth
        value={middlename}
        margin="normal"
        InputProps={{
         classes: {
          input: classes.tinyFont
         }
        }}
       />
       <TextField
        id="surname"
        label="Surname"
        onChange={this.onChangeFieldValue}
        className={classes.textField}
        fullWidth
        value={surname}
        margin="normal"
        InputProps={{
         classes: {
          input: classes.tinyFont
         }
        }}
       />
       <TextField
        id="email"
        label="Email"
        type="email"
        onChange={this.onChangeFieldValue}
        className={classes.textField}
        fullWidth
        value={email}
        margin="normal"
        InputProps={{
         classes: {
          input: classes.tinyFont
         }
        }}
        helperText="E.g: arcel.maendeleo@wfp.org"
       />
       <br />
       <br />
       <FormLabel component="legend">Gender</FormLabel>
       <RadioGroup row id="gender" aria-label="Gender" name="gender" className={classes.group} value={gender} onChange={this.handleChangeSelect}>
        <FormControlLabel className={classes.groupRadio} value="Male" control={<Radio color="primary" />} label="Male" />{" "}
        <FormControlLabel className={classes.groupRadio} value="Female" control={<Radio color="primary" />} label="Female" />
       </RadioGroup>
       <TextField
        id="bio"
        label="Bio / Description"
        onChange={this.onChangeFieldValue}
        className={classes.textField}
        fullWidth
        multiline
        value={bio}
        margin="normal"
        rows="4"
        InputProps={{
         classes: {
          input: classes.tinyFont
         }
        }}
        helperText="Say in brief about this person."
       />
       <br />
       <br />
       <Button type="submit" variant="contained" size="small" color="primary" margin="normal">
        Submit
       </Button>
      </form>
     </div>
     <ModalReducer this={This} />
    </Paper>
   </Fragment>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

Add.propTypes = {
 classes: PropTypes.object.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(Add);
