import { ModalReducer } from "../reducers";
import { React, PropTypes, Link, Fragment, Avatars } from "../includes/Commons";
import { styles, withStyles, Avatar, Fab, EditIcon, Paper, AppBar, Toolbar, DeleteIcon, Grid, SearchIcon, CardContent, Typography, Card, CardHeader, FavoriteIcon } from "../theme/styles/index";
import { userIsLoggedIn, fetchContact, likeContact, deleteContact } from "../includes/Functions";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class View extends React.Component {
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 state = {
  params: {},
  open: false,
  message: null,
  buttons: {},
  isUser: userIsLoggedIn(),
  contact: { vid: "", firstname: "", surname: "", email: "", bio: "", gender: "", timestamp: "", likes: "", fans: "" },
  anchorEl: null
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 static contextTypes = {
  router: PropTypes.object
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 componentDidMount() { this.setState({ params: this.props.match.params }); fetchContact(this);}
 handleClick = event => { this.setState({ anchorEl: event.currentTarget });};
 handleClose = () => { this.setState({ anchorEl: null }); };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {
  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  const This = this;
  const { classes } = This.props;
  const { isUser, buttons } = This.state;
  const { vid, firstname, surname, email, bio, gender, timestamp, likes, fans } = This.state.contact;
  const contains = buttons[vid] || fans.indexOf(isUser) !== -1;

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <Fragment>
    <Paper className={classes.paper}>
     <AppBar className={classes.searchBar} position="static" color="default" elevation={0}>
      <Toolbar>
       <Grid container spacing={16} alignItems="center">
        <Grid item>
         <SearchIcon className={classes.block} color="inherit" />
        </Grid>
       </Grid>
      </Toolbar>
     </AppBar>
     <div className={classes.contentWrapper}>
      <h3>{`${firstname} ${surname}`}</h3>
      <Card className={classes.card} key={vid}>
       <CardHeader
        avatar={
         <Avatar color="gray" name={firstname[0]} aria-label="Recipe" className={classes.avatar} size={32} round="20px">
          {firstname[0]}
         </Avatar>
        }
        action={<Fragment />}
        title={`${firstname} ${surname}`}
        subheader={Intl.DateTimeFormat("en-GB", {
         year: "numeric",
         month: "long",
         day: "2-digit"
        }).format(timestamp)}
       />
       <Avatars email={email} className={classes.avatarSpecial} name={`${firstname} ${surname}`} width="100%" size="330" color="gray" />
       <CardContent>
        <Typography component="p" className={classes.fontTiny}>
         Email | {email} | {likes} like(s)
        </Typography>
        <Typography component="p">{bio}</Typography>
        <Typography component="p">Gender : {gender}</Typography>
       </CardContent>
      </Card>
     </div>
     <ModalReducer this={This} />
    </Paper>
    <Fab aria-label="Like" color="primary" disabled={contains} className={classes.fab3} onClick={(event, e) => likeContact(This, vid)}>
     <FavoriteIcon />
    </Fab>
    <Fab aria-label="Delete" color="secondary" className={classes.fab} onClick={(e, vid, path) => deleteContact(This, vid, '/contacts')}>
     <DeleteIcon />
    </Fab>
    <Fab color="primary" aria-label="Edit" className={classes.fab2} component={Link} to={`/edit/${vid}`}>
     <EditIcon />
    </Fab>
   </Fragment>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

View.propTypes = {
 classes: PropTypes.object.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(View);
