import { SearchReducer } from "../reducers";
import { React, Fragment, PropTypes, Link } from "../includes/Commons";
import { styles, withStyles, AppBar, Toolbar, Paper, Grid, AddIcon, Fab } from "../theme/styles/index";
import { userIsLoggedIn, fetchRecentContacts, handleSearchInput } from "../includes/Functions";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class Home extends React.Component {
  
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 state = {
  search: "",
  anchorEl: null,
  isUser: userIsLoggedIn(),
  contacts: []
 };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 componentDidMount() { fetchRecentContacts(this); }
 handleTopSearchInput(event, e){ var check = handleSearchInput(event, e); if(check){ fetchRecentContacts(this); } }

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */



 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {
  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */
  
  const This = this;
  const { classes } = This.props;
  const { contacts } = This.state;

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <Fragment>
    <Paper className={classes.paper}>
     <AppBar className={classes.searchBar} position="static" color="default" elevation={0}>
      <Toolbar>
       <Grid container spacing={16} alignItems="center">
        <SearchReducer this={This} Change={(event, e) => this.handleTopSearchInput(event, This)} />
       </Grid>
      </Toolbar>
     </AppBar>
     <div className={classes.contentWrapper}>
      <h3>Recently Added Contacts</h3>
      <section id="timeline" className="timeline-outer">
       <div className="container" id="content">
        <div className="row">
         <div className="col s12 m12 l12">
          <ul className="timeline">
           {contacts.map(({ email, firstname, surname, vid, timestamp, gender, mobile, bio }) => (
            <li className="event" data-date={``} key={vid}>
             <Link className={classes.resetLink} to={`/view/${vid}`}><h2>{`${firstname.trim()} ${surname.trim()}`} </h2></Link>
             <div component="div" className={classes.fontTinySpecialP}>Email - {email}</div>
             <p style={{fontSize:14}}>{bio}</p>
             <div component="p" className={classes.fontTinySpecial}>
              {gender} - {Intl.DateTimeFormat("en-GB", { year: "numeric", month: "long", day: "2-digit" }).format(timestamp)} - Mobile : {mobile}
             </div>
            </li>
           ))}
          </ul>
         </div>
        </div>
       </div>
      </section>
     </div>
    </Paper>
    <Fab className={classes.fab} color="primary" component={Link} to="/add">
     <AddIcon />
    </Fab>
   </Fragment>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

Home.propTypes = {
 classes: PropTypes.object.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(Home);