 var fetchLikedContacts = async e => {
  const response = await fetch("https://graph-wbuddy.herokuapp.com/api/contacts/favorite/" + userIsLoggedIn() + "/?search=" + e.state.search);
  const data = await response.json();
  e.setState({ contacts: data });  
 };

