 var likeContact = async (e, vid, path) => {
  vid = vid ? vid : e.props.match.params.vid;
  const response = await fetch("https://graph-wbuddy.herokuapp.com/api/contacts/like/" + vid + "/" + userIsLoggedIn());
  const data = await response.json();
  e.setState({ buttons: { ...e.state.buttons, [vid]: true } }); 
  if (data.success) {
   if(path){e.context.router.history.push(path);}
   e.setState({ message: data.message });
   e.setState({ redirect: path });
  }
  handleOpenModal(e);
 };

