 var pageRedirect = function(e){
  if(!e.state.isUser && e.state.page !== e.state.loginPage && e.state.page !== e.state.registerPage && e.state.page !== e.state.accessPage){window.location.href = e.state.loginPage;}
  if(e.state.redirect && e.state.redirect !== e.state.page) {
   e.context.router.history.push(e.state.redirect);
   window.location.href = e.state.redirect;
   window.location.reload();
  }
 };
  
  /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 var handleOpenModal = function(e) {
  e.setState({ open: true });
 };

