 var eachRecursive = function(obj, prefix) {
  for (var k in obj) {
   if (typeof obj[k] == "object" && obj[k] !== null) {
    eachRecursive(obj[k], prefix);
   } else if (typeof obj[k] == "string") {
    localStorage.setItem("wbuddy." + k, obj[k]);
   }
  }
 };

