 var deleteContact = async (e, vid, path) => {
  vid = vid ? vid : e.props.match.params.vid;
  const response = await fetch("https://graph-wbuddy.herokuapp.com/api/contacts/delete/" + vid);
  const data = await response.json();
  e.setState({ message: data.message });
  handleOpenModal(e);
  if (data.success) { 
   if(path){e.setState({ redirect: path });}
  }
 };
 

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */
 
 var insertContact = async (e, path) => {
  var params = e.state.contact;
  var query = Object.keys(params).map(k => encodeURIComponent(k) + "=" + encodeURIComponent(params[k])).join("&");
  const response = await fetch("https://graph-wbuddy.herokuapp.com/api/contacts/insert/?" + query);
  const data = await response.json();
  e.setState({ message: data.message });
  handleOpenModal(e);
  if (data.success) { e.setState({ redirect: path });}
 };

  /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */
 
 var registerUser = async (e, path) => {
  var md5 = require('md5');
  var params = e.state.account;
  params.password = md5(params.password);
  var query = Object.keys(params).map(k => encodeURIComponent(k) + "=" + encodeURIComponent(params[k])).join("&");
  const response = await fetch("https://graph-wbuddy.herokuapp.com/api/register/?" + query);
  const data = await response.json();
  e.setState({ message: data.message });
  handleOpenModal(e);
  if (data.success) { 
   localStorage.setItem('wbuddy.signUp', true);
   e.context.router.history.push(path);
   e.setState({ redirect: path });
  }
 };


  /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 var editContact = async (e, path) => {
 var params = e.state.contact;
 var query = Object.keys(params).map(k => encodeURIComponent(k) + '=' + encodeURIComponent(params[k])).join('&');
 const response = await fetch('https://graph-wbuddy.herokuapp.com/api/contacts/edit/'+params.vid+'/?' + query);
 const data = await response.json();
 e.setState({ buttons: { ...e.state.buttons, [params.vid]: true } }); 
 e.setState({ message: data.message }); 
 handleOpenModal(e);
 if(data.success) { e.setState({ redirect: path });}
}
 
