import { ModalReducer } from "../reducers";
import { React, Fragment, PropTypes } from "../includes/Commons";
import { styles, withStyles, AppBar, Toolbar, Paper, Grid, TextField, Button } from "../theme/styles/index";
import { loginUser } from "../includes/Functions";

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

class Access extends React.Component {
 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 constructor(props) {
  super(props);
  this.onSubmit = this.onSubmit.bind(this);
  this.onChangeFieldValue = this.onChangeFieldValue.bind(this);
  this.state = { message: null, open: false, redirect: false, account: { username: "", password: "" }};
 }

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 static contextTypes = { router: PropTypes.object };
 onSubmit(e) { e.preventDefault(); loginUser(this, '/home'); }
 onChangeFieldValue(event, value) { this.setState({ account: { ...this.state.account, [event.target.id]: event.target.value } }); }
 handleChangeSelect = (event, value) => { this.setState({ account: { ...this.state.account, [event.target.name]: value } }); };

 /*
  * This code is developed to demonstrate the use of ReactJS and ReactNatice
  * The deelopment also allows me to demonstrate my capabilities using the framework
  * Following create-react-app methods, the file structure is made from scratch
  * Copyright - World Food Programmes - Digital Transformation
  */

 render() {
  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  const This = this;
  const { classes } = This.props;
  const { username, password } = This.state.account;

  /*
   * This code is developed to demonstrate the use of ReactJS and ReactNatice
   * The deelopment also allows me to demonstrate my capabilities using the framework
   * Following create-react-app methods, the file structure is made from scratch
   * Copyright - World Food Programmes - Digital Transformation
   */

  return (
   <Fragment>
    <Paper className={classes.paper}>
     <AppBar className={classes.searchBar} position="static" color="default" elevation={0}>
      <Toolbar>
       <Grid container spacing={16} alignItems="center" />
      </Toolbar>
     </AppBar>
     <div className={classes.contentWrapper}>
      <h3>Login to the system</h3>
      <form className={classes.container} noValidate autoComplete="off" onSubmit={this.onSubmit}>
       <TextField
        id="username"
        type="email"
        label="Username or Email"
        onChange={this.onChangeFieldValue}
        className={classes.textField}
        fullWidth
        value={username}
        margin="normal"
        InputProps={{
         classes: {
          input: classes.tinyFont
         }
        }}
        helperText="E.g: arcel.maendeleo@wfp.org"
       />

      <TextField
        id="password"
        label="Password"
        type="password"
        onChange={this.onChangeFieldValue}
        className={classes.textField}
        fullWidth
        value={password}
        margin="normal"
        InputProps={{
         classes: {
          input: classes.tinyFont
         }
        }}
        helperText="E.g: arcel.maendeleo@wfp.org"
       />
       <br />
       <br />
       <Button type="submit" variant="contained" size="small" color="primary" margin="normal">
        Login
       </Button>
      </form>
     </div>
     <ModalReducer this={This} />
    </Paper>
   </Fragment>
  );
 }
}

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

Access.propTypes = {
 classes: PropTypes.object.isRequired
};

/*
 * This code is developed to demonstrate the use of ReactJS and ReactNatice
 * The deelopment also allows me to demonstrate my capabilities using the framework
 * Following create-react-app methods, the file structure is made from scratch
 * Copyright - World Food Programmes - Digital Transformation
 */

export default withStyles(styles)(Access);
